import React, { useEffect, useRef, useState } from "react";

const maxWidth = 7;
export const HomeDark = () => {
  const [isVowel, setIsVowel] = useState(false);
  return (
    <section className="home-dark fixed flex flex-col justify-center items-stretch  bg-gray-900 h-3/5 sm:h-3/4 transition-all w-full overflow-hidden  z-10">
      <div className="select-none block text-gray-300 text-2xl sm:text-4xl text-center relative -top-5 ">
        I'm A{isVowel && "n"} <br />
        <TextScrollY vowel={setIsVowel} />
      </div>
    </section>
  );
};

const TextScrollY = ({ vowel }) => {
  const [currId, setCurrId] = useState(0);
  const iAmA = [
    {
      id: 0,
      desc: "Software Developer",
    },
    {
      id: 1,
      desc: "researcher",
    },
    {
      id: 2,
      desc: "Tech enthusiast",
    },
    {
      id: 3,
      desc: "Engineer",
    },
    {
      id: 4,
      desc: "Tinkerer",
    },
  ];

  useEffect(() => {
    const intervalRef = setInterval(() => {
      setCurrId((prev) => {
        return (prev + 1) % iAmA.length;
      });
    }, 2250);

    return () => {
      clearInterval(intervalRef);
    };
  }, []);

  // Render the required elements
  const renderElements = iAmA.reduce((prev, curr) => {
    const len = iAmA.length;
    if ((currId + 1 + len) % len === curr.id) {
      return [...prev, { ...curr, type: "next" }];
    } else if (currId === curr.id) {
      return [...prev, { ...curr, type: "curr" }];
    } else {
      return [...prev, { ...curr, type: "prev" }];
    }
  }, []);

  console.log(renderElements);

  return (
    <div className="select-none relative flex justify-center h-11 sm:h-20 overflow-hidden w-full">
      {renderElements.map((item) => {
        var str = item.desc;
        var vowelRegex = "^[aieouAIEOU].*";
        var matched = str.match(vowelRegex);
        if (matched) {
          vowel(false);
        } else {
          vowel(true);
        }
        return (
          <h3
            className={`absolute text-4xl sm:text-6xl font-bold duration-300 transition-all capitalize ${item.type}-slider`}
            key={item.id}
          >
            {item.desc}
          </h3>
        );
      })}
    </div>
  );
};
export default HomeDark;
